.b-item {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  border-radius: 5px;
  -webkit-box-shadow: 0.2px 0.2px 0.2px rgba(0, 0, 0, 0.2), -0.2px -0.2px 0.2px rgba(0, 0, 0, 0.2);
          box-shadow: 0.2px 0.2px 0.2px rgba(0, 0, 0, 0.2), -0.2px -0.2px 0.2px rgba(0, 0, 0, 0.2);
  background: #fff; }

.item-inner {
  display: block;
  padding: 25px 0;
  width: 100%;
  text-decoration: none;
  cursor: pointer; }

.item-media {
  position: relative;
  margin-bottom: 3.125rem;
  padding: 0 1.5625rem;
  width: 100%; }

.item-textbox {
  padding: 0 1.5625rem; }

.item-header {
  position: relative;
  margin-bottom: 2.1875rem;
  color: #242d3b; }
  .item-header:after {
    content: '';
    position: absolute;
    bottom: -0.9375rem;
    left: 0;
    width: 1.875rem;
    height: 0.3125rem;
    background: #242d3b; }

.item-copytext {
  font: 400 1.125rem/1.6 Montserrat, sans-serif;
  margin-bottom: 1.5625rem;
  word-wrap: break-word;
  color: #242d3b; }
  @media (min-width: 25em) and (max-width: 34.9999em) {
    .item-copytext {
      font-size: 0.875rem; } }
  @media (min-width: 35em) and (max-width: 49.9999em), (min-width: 50em) and (max-width: 67.9999em), (min-width: 68em) and (max-width: 86.9999em) {
    .item-copytext {
      font-size: 0.875rem;
      font-size: calc(0.875rem + 0.25 * (100vw - 37.5rem) / 52.5); } }
  @media (min-width: 87em) and (max-width: 115.9999em) {
    .item-copytext {
      font-size: 1.125rem;
      font-size: calc(1.125rem + 0.25 * (100vw - 90.0625rem) / 29.9375); } }
  @media (min-width: 116em) {
    .item-copytext {
      font-size: 1.5rem; } }

.item-placeholder {
  width: 100%;
  height: 100%; }

.item-placeholder-item {
  fill: #9a9da1; }



