.b-navoverlay {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  background: #171f28;
  opacity: 0;
  -webkit-transition: opacity 300ms ease-out;
  -o-transition: opacity 300ms ease-out;
  transition: opacity 300ms ease-out;
  z-index: 0; }
  .b-navoverlay.is-visible {
    opacity: 1;
    z-index: 1; }

.navoverlay-inner {
  margin: auto;
  padding: 3.125rem; }

.navoverlay-list {
  list-style-type: none; }

.navoverlay-item {
  font-size: 0.8125rem;
  font-size: calc(0.8125rem + 0.1875 * (100vw - 18.75rem) / 29.25);
  font-family: Montserrat, sans-serif;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase; }

.navoverlay-item-inner {
  display: block;
  padding: 1.5625rem 1.25rem;
  text-decoration: none;
  color: #f2f2f2;
  -webkit-transition: 350ms ease-out color;
  -o-transition: 350ms ease-out color;
  transition: 350ms ease-out color; }
  .navoverlay-item-inner:hover, .navoverlay-item-inner:focus {
    color: #59cd90; }



